import React, { useState } from 'react';
import PropTypes from 'prop-types';

import TextScroll from '../../components/TextScroll';

import './bio.scss';
import GalleryMin from '../../components/GalleryMin';

const Bio = () => {
  // current
  const [ current, setCurrent ] = useState(0);
  // last
  const [ last, setLast ] = useState(0);

  // props
  //const { page: { gallery } } = props;

  const gallery = [
    {
      title: '1',
      src: process.env.PUBLIC_URL + '/images/pollyanna-bio-gallery.jpg'
    }, {
      title: '2',
      src: process.env.PUBLIC_URL + '/images/pollyanna-bio-gallery-2.jpg'
    }, {
      title: '2',
      src: process.env.PUBLIC_URL + '/images/pollyanna-bio-gallery-3.jpg'
    }, {
      title: '2',
      src: process.env.PUBLIC_URL + '/images/pollyanna-bio-gallery-4.jpg'
    }, {
      title: '2',
      src: process.env.PUBLIC_URL + '/images/pollyanna-bio-gallery-5.jpg'
    }, {
      title: '2',
      src: process.env.PUBLIC_URL + '/images/pollyanna-bio-gallery-6.jpg'
    }, {
      title: '2',
      src: process.env.PUBLIC_URL + '/images/pollyanna-bio-gallery-7.jpg'
    }, {
      title: '2',
      src: process.env.PUBLIC_URL + '/images/pollyanna-bio-gallery-8.jpg'
    }, {
      title: '2',
      src: process.env.PUBLIC_URL + '/images/pollyanna-bio-gallery-9.jpg'
    }, {
      title: '2',
      src: process.env.PUBLIC_URL + '/images/pollyanna-bio-gallery-10.jpg'
    }, {
      title: '2',
      src: process.env.PUBLIC_URL + '/images/pollyanna-bio-gallery-11.jpg'
    }
  ];

  // on set item
  const onSetItem = (index) => {
    if (Number.isInteger(index) === true) {
      setLast(current);
      setCurrent(index);
    }
  };

  // on next prev
  const onNextPrev = (e = 'prev') => {
    let index = 0;
    const total = gallery.length - 1;

    if (e === 'prev') {
      index = (current - 1) < 0 ? total : current - 1;
    } else if (e === 'next') {
      index = (current + 1) > total ? 0 : (current + 1);
    }

    onSetItem(index);
  };

  // return
  return (
    <div className="page bio">
      <div className="wrapper">
        <GalleryMin
          current={current}
          last={last}
          onNextPrev={onNextPrev}
          setCurrent={setCurrent}
          items={gallery}
          type={2} />

        <TextScroll width={500} height={300} type={2}>
          <p>
            Cantora carioca formada em Musicoterapia pelo &nbsp;
            <a href="http://www.cbmmusica.edu.br/" className="link">Conservatrio Brasileiro de Msica</a>, onde estudou tambm canto lrico e violo. Porm, foi no samba, na bossa nova, na msica brasileira que se encontrou e faz dela sua expresso artstica.
          </p>
          <p>
            Pollyanna estudou canto na &nbsp;
            <a href="http://www.casadochoro.com.br/portal/view/escola_portatil" className="link">Escola Porttil de Choro</a>.&nbsp;
            Vocalista do <strong>Grupo Fala Brasil</strong> de samba e choro durante 10 anos.</p>
          <p>Vocalista do bloco Musicalidade, que leva carnaval para asilos de idosos. Comps trilha sonora para documentrios e peas teatrais.</p>
          <p>Entrevistas para <strong>NY Times</strong>, <strong>Canal Futura</strong>, <strong>Programa Estrelas (Rede Globo)</strong>, <strong>Documentrio "Pollyanna Ferrari - Canto dos Cantos" (TV Pinel), Radio Multi Rio</strong>.</p>
          <p>Tem se apresentado h 15 anos no Rio de Janeiro, alm de shows na Frana em 2015, 2016 e 2017 e em Portugal em 2018.</p>  
          <p>Graduou-se em Musicoterapia pelo Conservatrio Brasileiro de Msica, especializou-se em Sade Mental pela UFRJ, onde tambm fez Mestrado em Psicologia Social.</p>
          <p>Assessora Tcnica dos projetos de cultura e gerao de renda da Rede de Sade Mental Prefeitura (RJ) (2014-2017). Professora da disciplina "Musicoterapia e Cultura" da Ps Graduao em Musicoterapia (CBM-CEU). Consultora do Ecomuseu Nega Vilma  Comunidade Santa Marta (desde 2009).</p>
          <p>Na <a href="http://www.amtrj.com.br/" className="link">Associao de Musicoterapia do Estado do Rio de Janeiro</a> foi Presidente, Vice Presidente, 2 Secretria e membro do Conselho Consultivo.</p>
          <p>Coordenadora do Coletivo Carnavalesco T Pirando, Pirado, Pirou!, bloco de carnaval da rede pblica de sade mental durante 5 anos.</p>
          <p>Membro <strong>Fundadora do Coletivo Porto Ldico</strong> onde desenvolve musicoterapia comunitria com crianas e jovens do Instituto Profissional o Tero, no Porto.</p>
        </TextScroll>
      </div>
    </div>
  )
};

Bio.propTypes = {
  any: PropTypes.any,
}

export default Bio;